.problem-popup {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: flex-end;
}

.problem-popup-content {
  width: 100%;
  height: 70vh;
  background-color: #fff;
}

.ppc-header-info {
  margin: 0 auto;
  width: 92%;
  height: 90px;
  border-bottom: 1px solid #f0f0f0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.ppchi-box {
  margin-right: 40px;
}

.ppchi-box > span:nth-of-type(1) {
  margin-right: 10px;
  font-size: 30px;
  color: #333;
}

.ppchi-box > span:nth-of-type(2) {
  font-size: 30px;
  color: #18b4ed;
}

.ppc-content-box {
  margin: 0px auto;
  width: 92%;
  height: calc(100% - 90px);
  overflow-y: scroll;
  display: flex;
  flex-wrap: wrap;
}

.ppc-content-box-item {
  margin: 30px 15px 0 0;
  width: calc(calc(100% / 6) - 15px);
  height: 80px;
  border-radius: 10px;
  background-color: #d2d2d2;
  font-size: 28px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.ppcc-f {
  width: 100%;
  height: 30px;
}

.problem-popup-item-border {
  border: 1px solid #333;
}

.problem-popup-item-selected {
  background-color: #18b4ed;
}
